@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.section-a {
  background-color: burlywood;
}

.section-x {
  background-color: darkolivegreen;
}

.footer {
  background-color: aqua;
  height: 40vh;
}



.container {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    text-align: center;
    color: white;
    background-color: #222a30;
}

.into-name {
    font-size: 4rem;
}

.into-title {
    font-size: 2rem;
}

.flexitem-scroll-down {
    -webkit-align-self: center;
            align-self: center;
}

.mouse {
    background: #4e5559 linear-gradient(transparent 0%, transparent 50%, #fff 50%, #fff 100%);
    position: relative;
    width: 34.6px;
    height: 58.6px;
    border-radius: 100px;
    background-size: 100% 200%;
    -webkit-animation: colorSlide 7s linear infinite, nudgeMouse 7s ease-out infinite;
            animation: colorSlide 7s linear infinite, nudgeMouse 7s ease-out infinite;
}
.mouse:before, .mouse:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
}
.mouse:before {
    width: 30.6px;
    height: 54.6px;
    background-color: #222a30;
    border-radius: 100px;
}
.mouse:after {
    background-color: #fff;
    width: 6.6px;
    height: 6.6px;
    border-radius: 100%;
    -webkit-animation: trackBallSlide 7s linear infinite;
            animation: trackBallSlide 7s linear infinite;
}
@-webkit-keyframes colorSlide {
    0% {
        background-position: 0% 100%;
   }
    20% {
        background-position: 0% 0%;
   }
    21% {
        background-color: #4e5559;
   }
    29.99% {
        background-color: #fff;
        background-position: 0% 0%;
   }
    30% {
        background-color: #4e5559;
        background-position: 0% 100%;
   }
    50% {
        background-position: 0% 0%;
   }
    51% {
        background-color: #4e5559;
   }
    59% {
        background-color: #fff;
        background-position: 0% 0%;
   }
    60% {
        background-color: #4e5559;
        background-position: 0% 100%;
   }
    80% {
        background-position: 0% 0%;
   }
    81% {
        background-color: #4e5559;
   }
    90%, 100% {
        background-color: #fff;
   }
}
